/* Menu */
#menu-container {
  display: none;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 6;
  font-size: 2rem;
  background-color: var(--nav-background-clr-);
}

#menu-container.show {
  display: flex;
}

.menu-top {
  position: absolute;
  top: 0;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  font-size: 1.5rem;
}

.menu-top--items {
  height: 100%;
  padding: 16px 0 20px 11px;
}

.menu-items {
  width: 100%;
}

.menu-items ul li a {
  text-decoration: none;
  color: var(--menu-text-);
}

.menu-items ul li {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 0;
}

.menu-items--social ul {
  display: flex;
  justify-content: center;
}

.menu-items--social ul li {
  padding: 30px 60px;
}

@media (min-width: 720px) {
  #menu-container {
    display: none;
  }
}
